@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  background-color: #171717;
  color: whitesmoke;
  font-family: Poppins;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

