.home-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.name-container {
  cursor: pointer;
  font-size: 4vw;
  font-weight: 800;
  animation: fade-in-fwd 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  /* text-indent: 0.1vw; */
}

.first-name {
  margin-right: 1vw;
}

.name-container,
.first-name,
.last-name {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
}

.name-letter {
  transition: 0.1s ease;
}

.name-letter:hover {
  transform: scale(1.15);
}

.home-discription {
  margin-top: 10px;
  font-size: 2vw;
  height: 50px;
  font-family: Raleway;
  font-style: normal;
}

.social-media-icons {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  left: 5%;
  transform: translateY(-50%);
  animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.social-media-icons svg,
.message-icons svg {
  margin: 5px;
  color: whitesmoke;
  opacity: 0.8;
  transition: 0.2s ease;
}

.social-media-icons svg:hover,
.message-icons svg:hover {
  opacity: 1;
  transform: scale(1.2);
}

.social-media-icons a,
.message-icons a {
  outline: none;
}

.message-icons {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  right: 5%;
  transform: translateY(-50%);
  animation: slide-in-right 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@keyframes fade-in-fwd {
  0% {
    transform: translateZ(-80px);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}

@keyframes slide-in-left {
  0% {
    transform: translateX(-1000px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes slide-in-right {
  0% {
    transform: translateX(1000px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.about-container {
  margin-top: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.color-change {
  color: #04a777;
}

.about-h1 {
  font-family: raleway;
  font-size: 3vw;
}

.about-details {
  margin-top: 45px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.about-details-text {
  max-width: 40vw;
  margin-right: 15vw;
}
.about-details h2 {
  font-size: 2.5vw;
}

.about-details p {
  font-size: 1.5vw;
  text-align: justify;
}

.about-details img {
  max-width: 400px;
  border-radius: 10px;
}

.my-services {
  margin-top: 5vw;
  width: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.services {
  margin-top: 5vw;
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.services-box {
  background-color: #191919;
  width: 30vw;
  height: 30vw;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0.5s ease-in;
}

.icon-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* margin-top: 2vw; */
  background-color: #04a777;
  border-radius: 30px;
  width: 8vw;
  margin-top: 2vw;
  height: 8vw;
}

.services img {
  max-height: 5vw;
  width: auto;
}

.services h2 {
  margin-top: 3vw;
  font-size: 2.5vw;
}

.services p {
  font-size: 1.5vw;
  text-align: center;
  max-width: 25vw;
  opacity: 0.8;
}

.services-box:hover {
  background-color: #04a777;
}

.skills {
  margin-top: 5vw;
  width: 80vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#skill-bars {
  margin: 2vh;
  padding: 25px 30px;
  width: 40vw;
  /* background: rgb(66, 66, 66); */
  /* box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2); */
  border-radius: 10px;
}
#skill-bars #bar {
  margin: 20px 0;
}
#skill-bars #bar:first-child {
  margin-top: 0px;
}
#skill-bars #bar #info {
  margin-bottom: 5px;
}
#skill-bars #bar #info .span {
  font-weight: 500;
  font-size: 17px;
  opacity: 0;
  animation: showText 0.5s 1s linear forwards;
}
@keyframes showText {
  100% {
    opacity: 1;
  }
}
#skill-bars #bar #progress-line {
  height: 10px;
  width: 100%;
  background: #f0f0f0;
  position: relative;
  transform: scaleX(0);
  transform-origin: left;
  border-radius: 10px;
  /* box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05), */
  /* 0 1px rgba(255, 255, 255, 0.8); */
  animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}
@keyframes animate {
  100% {
    transform: scaleX(1);
  }
}
#bar #progress-line .span {
  height: 100%;
  position: absolute;
  border-radius: 10px;
  transform: scaleX(0);
  transform-origin: left;
  background: #04a777;
  animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}
#bar #progress-line.html .span {
  width: 90%;
}
#bar #progress-line.css .span {
  width: 80%;
}
#bar #progress-line.javascript .span {
  width: 70%;
}
#bar #progress-line.cpp .span {
  width: 70%;
}
#bar #progress-line.react .span {
  width: 80%;
}
#bar #progress-line.python .span {
  width: 60%;
}
#progress-line .span::before {
  position: absolute;
  content: "";
  top: -10px;
  right: 0;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-bottom-width: 0px;
  border-right-width: 0px;
  border-top-color: #000;
  opacity: 0;
  animation: showText2 0.5s 1.5s linear forwards;
}
#progress-line .span::after {
  position: absolute;
  top: -28px;
  right: 0;
  font-weight: 500;
  background: #000;
  color: #fff;
  padding: 1px 8px;
  font-size: 12px;
  border-radius: 3px;
  opacity: 0;
  animation: showText2 0.5s 1.5s linear forwards;
}
@keyframes showText2 {
  100% {
    opacity: 1;
  }
}
#progress-line.html .span::after {
  content: "90%";
}
#progress-line.css .span::after {
  content: "80%";
}
#progress-line.javascript .span::after {
  content: "70%";
}
#progress-line.cpp .span::after {
  content: "70%";
}
#progress-line.react .span::after {
  content: "80%";
}
#progress-line.python .span::after {
  content: "60%";
}

.contact-container {
  margin-top: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Poppins;
  color: whitesmoke;
}

.contact-deatils {
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.contact-me {
  font-family: raleway;
  font-size: 3vw;
}

.contact-form {
  width: 30vw;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-form form {
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;
}

.contact-form h3,
.contact-deatils h3 {
  font-size: 1.5vw;
}

.contact-form input,
.contact-form textarea {
  min-width: 30vw;
  max-width: 30vw;
  height: auto;
  max-height: 15vh;
  min-height: 4vh;
  padding: 1vw;
  background-color: black;
  border-radius: 10px;
  border: none;
  margin-bottom: 25px;
  font-family: Poppins;
  /* font-weight: bold; */
  font-size: 1.2vw;
  color: whitesmoke;
}

.contact-form button {
  background-color: #04a777;
  width: 8vw;
  padding: 0.5vw 1vw;
  border: none;
  font-family: Poppins;
  font-weight: bold;
  font-size: 1.2vw;
  border-radius: 10px;
  color: whitesmoke;
}

::-webkit-input-placeholder {
  color: whitesmoke;
}

:-ms-input-placeholder {
  color: whitesmoke;
}

::placeholder {
  color: whitesmoke;
}

.contact-info {
  min-width: 30vw;
  height: auto;
  border-radius: 20px;
  background-color: #04a777;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.info-cards {
  display: grid;
  grid-template-rows: 2vw 2vw;
  grid-template-columns: 5vw 12vw;
  align-items: center;
  justify-items: start;
  margin-bottom: 25px;
}
.info-cards svg {
  font-size: 4vw;
  grid-row: 1 / 3;
  grid-column: 1 / 2;
}

.info-cards p {
  grid-row: 2/3;
  grid-column: 2/3;
  font-size: 1vw;
}

.info-cards h4 {
  font-size: 1.2vw;
}

.contact-form input:focus {
  border: #04a777 solid;
  border-width: thin;
  outline: none;
}

.contact-form textarea:focus {
  border: #04a777 solid;
  border-width: thin;
  outline: none;
}

.contact-form button:hover {
  background-color: #049b6d;
}

.portfolio-container {
  margin-top: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.my-portfolio {
  font-family: raleway;
  font-size: 3vw;
}


.navbar-container {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0%;
  left: 0%;
  background-color: black !important;
  transform: translateX(100%);
  transition: transform 0.5s ease-in;
  z-index: 1;
}

.navbar-active {
  transform: translateX(0);
}

.nav-list {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  list-style: none;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 72px;
}

.nav-list li {
  transition: 0.2s ease-in;
}

.nav-list li a {
  text-decoration: none;
  color: whitesmoke;
}

.hamburger {
  position: fixed;
  right: 5%;
  top: 10%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  z-index: 1;
}

.hamburger-lines {
  height: 4px;
  margin: 4px;
  border-radius: 3px;
  background-color: whitesmoke !important;
  transition: 0.2s ease-in;
}

.hamburger-1 {
  width: 50px;
}
.hamburger-2 {
  width: 35px;
}
.hamburger-3 {
  width: 20px;
}

.hamburger:hover .hamburger-lines {
  /* transform: skewX(10deg); */
  transform: scaleX(1.1);
  background-color: #04a777;
}

.nav-list li:hover {
  transform: scale(1.1);
}
.nav-list li a:hover {
  color: #04a777;
}

